<script>
import $ from 'jquery'
export default {
  data: () => ({
    valid_thongtinchuhoso: false,
    citys: [],
    delegateDistricts: [],
    districts: [],
    delegateWards: [],
    wards: [],
    applicants: [],
    delegateApplicants: [],
    searchApplicantDelegate: '',
    labelSwitch: {
      'true': {
        cmtnd: 'CMND/ Hộ chiếu',
        nguoi_nop: 'Họ và tên'
      },
      'false': {
        cmtnd: 'Mã số thuế',
        nguoi_nop: 'Tên tổ chức/ cá nhân'
      }
    },
    searchApplicant: ''
  }),
  computed: {
    loading () {
      return this.$store.getters.loading
    },
    thongTinChuHoSo () {
      return this.$store.getters.thongTinChuHoSo
    },
    thongTinNguoiNopHoSo () {
      return this.$store.getters.thongTinNguoiNopHoSo
    }
  },
  created () {
    var vm = this
    vm.$nextTick(function () {
      var filter = {
        collectionCode: 'ADMINISTRATIVE_REGION',
        level: 0,
        parent: 0
      }
      vm.$store.getters.getDictItems(filter).then(function (result) {
        vm.citys = result.data
      })
      setTimeout(function () {
        var thongTinChuHoSoTemp = vm.$store.getters.thongTinChuHoSo
        console.log('thongTinChuHoSoTemp------------', thongTinChuHoSoTemp)
        if (thongTinChuHoSoTemp.cityCode) {
          filter.parent = thongTinChuHoSoTemp.cityCode
          filter.level = 1
          vm.$store.getters.getDictItems(filter).then(function (result) {
            vm.districts = result.data
          })
        }
        if (thongTinChuHoSoTemp.districtCode) {
          filter.parent = thongTinChuHoSoTemp.districtCode
          filter.level = 1
          vm.$store.getters.getDictItems(filter).then(function (result) {
            vm.wards = result.data
          })
        }
        var thongTinNguoiNopHoSoTemp = vm.$store.getters.thongTinNguoiNopHoSo
        console.log('thongTinNguoiNopHoSoTemp------------', thongTinNguoiNopHoSoTemp)
        if (thongTinNguoiNopHoSoTemp.delegateCityCode) {
          filter.parent = thongTinNguoiNopHoSoTemp.delegateCityCode
          filter.level = 1
          vm.$store.getters.getDictItems(filter).then(function (result) {
            vm.delegateDistricts = result.data
          })
        }
        if (thongTinNguoiNopHoSoTemp.delegateDistrictCode) {
          filter.parent = thongTinNguoiNopHoSoTemp.delegateDistrictCode
          filter.level = 1
          vm.$store.getters.getDictItems(filter).then(function (result) {
            vm.delegateWards = result.data
          })
        }
      }, 200)
    })
  },
  watch: {
    thongTinChuHoSo: {
      handler: function () {
        var vm = this
        let value = vm.thongTinChuHoSo
        let tempData = {
          delegateName: value.applicantName,
          delegateCityCode: value.cityCode,
          delegateAddress: value.address,
          delegateDistrictCode: value.districtCode,
          delegateWardCode: value.wardCode,
          delegateEmail: value.contactEmail,
          delegateTelNo: value.contactTelNo,
          delegateIdNo: value.applicantIdNo
        }
        vm.delegateApplicants = vm.applicants
        this.$store.dispatch('setThongTinNguoiNopHoSo', tempData)
      },
      deep: true
    },
    thongTinNguoiNopHoSo: {
      handler: function () {
        var vm = this
        let value = vm.thongTinNguoiNopHoSo
        if (value.sameUser) {
          this.$store.dispatch('setThongTinNguoiNopHoSo', vm.thongTinChuHoSo)
        } else {
          this.$store.dispatch('resetThongTinNguoiNopHoSo')
        }
      },
      deep: true
    },
    searchApplicant (value) {
      var vm = this
      let data = {
        keyword: value,
        type: '',
        lock: ''
      }
      vm.$store.dispatch('searchApplicant', data).then(function (result) {
        vm.applicants = result
        if (vm.thongTinNguoiNopHoSo.sameUser) {
          vm.delegateApplicants = result
        }
      }).catch(function (xhr) {
        console.log(xhr)
        vm.applicants = []
        vm.delegateApplicants = []
      })
    },
    searchApplicantDelegate (value) {
      var vm = this
      let data = {
        keyword: value,
        type: '',
        lock: ''
      }
      vm.$store.dispatch('searchApplicant', data).then(function (result) {
        vm.delegateApplicants = result
      }).catch(function (xhr) {
        console.log(xhr)
        vm.delegateApplicants = []
      })
    }
  },
  methods: {
    onChangeCity (data) {
      var vm = this
      let filter = {
        collectionCode: 'ADMINISTRATIVE_REGION',
        level: 1,
        parent: data
      }
      vm.$store.commit('setCityVal', data)
      vm.$store.getters.getDictItems(filter).then(function (result) {
        vm.districts = result.data
        vm.wards = []
        if (vm.thongTinNguoiNopHoSo.sameUser) {
          vm.delegateDistricts = result.data
          vm.wards = []
        }
      })
    },
    onChangeDistrict (data) {
      var vm = this
      let filter = {
        collectionCode: 'ADMINISTRATIVE_REGION',
        level: 1,
        parent: data
      }
      console.log(data)
      vm.$store.commit('setDistrictVal', data)
      vm.$store.getters.getDictItems(filter).then(function (result) {
        vm.wards = result.data
        if (vm.thongTinNguoiNopHoSo.sameUser) {
          vm.delegateWards = result.data
        }
      })
    },
    onChangeWard (data) {
      this.$store.commit('setWardVal', data)
    },
    onChangeApplicantIdNo (data) {
      // this.$store.dispatch('getUserInfoFromApplicantIdNo', data)
    },
    onChangeDelegateCity (data) {
      var vm = this
      let filter = {
        collectionCode: 'ADMINISTRATIVE_REGION',
        level: 1,
        parent: data
      }
      vm.$store.getters.getDictItems(filter).then(function (result) {
        vm.delegateDistricts = result.data
        if (vm.thongTinNguoiNopHoSo.sameUser) {
          vm.districts = result.data
        }
      })
    },
    onChangeDelegateName (data) {
      var vm = this
      console.log(data)
      if (data.applicantName) {
        var applicantsTmp = {
          delegateName: data.applicantName,
          delegateEmail: data.contactEmail,
          delegateTelNo: data.contactTelNo,
          delegateIdNo: data.applicantIdNo,
          delegateCityCode: data.cityCode,
          delegateDistrictCode: data.districtCode,
          delegateWardCode: data.wardCode
        }
        if (data.cityCode) {
          vm.onChangeDelegateCity(data.cityCode)
        }
        if (data.districtCode) {
          vm.onChangeDelegateDistrict(data.districtCode)
        }
        var dataTemp = Object.assign(vm.$store.getters.thongTinNguoiNopHoSo, applicantsTmp)
        vm.$store.dispatch('setThongTinNguoiNopHoSo', dataTemp)
      }
      console.log(applicantsTmp)
    },
    onChangeApplicantName (data) {
      console.log(data)
      var vm = this
      console.log(data)
      if (data.applicantName) {
        var applicantsTmp = {
          applicantName: data.applicantName,
          contactEmail: data.contactEmail,
          contactTelNo: data.contactTelNo,
          applicantIdNo: data.applicantIdNo,
          cityCode: data.cityCode,
          districtCode: data.districtCode,
          wardCode: data.wardCode
        }
        if (data.cityCode) {
          vm.onChangeCity(data.cityCode)
        }
        if (data.districtCode) {
          vm.onChangeDistrict(data.districtCode)
        }
        if (data.applicantIdNo) {
          $('#applicantIdNo').val(data.applicantIdNo)
        }
        var dataTemp = Object.assign(vm.$store.getters.thongTinChuHoSo, applicantsTmp)
        vm.$store.dispatch('setThongTinChuHoSo', dataTemp)
      }
      console.log(applicantsTmp)
    },
    onChangeDelegateDistrict (data) {
      var vm = this
      let filter = {
        collectionCode: 'ADMINISTRATIVE_REGION',
        level: 1,
        parent: data
      }
      vm.$store.getters.getDictItems(filter).then(function (result) {
        vm.delegateWards = result.data
        if (vm.thongTinNguoiNopHoSo.sameUser) {
          vm.wards = result.data
        }
      })
    }
  }
}
</script>



